.Playlist {
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-y: scroll;
    width: 40%;
    height: 950px;
    padding: 1.5rem 1.16rem;
    background-color: rgba(0, 0, 0, 1);
    border: 2rem solid black;
    border-radius: 2rem;
  }
  
  .Playlist input {
    width: 80%;
    margin-bottom: 1rem;
    text-align: center;
    border: 0;
    outline: 0;
    background: transparent;
    border-bottom: 1px solid #6f6f6f;
    font-family: 'Poppins', sans-serif;
    font-size: 1.55rem;
    color: #fff;
  }
  
  .Playlist-save {
    cursor: pointer;
    width: 10rem;
    padding: .77rem 0;
    border-radius: 54px;
    border-width: 0px;
    margin-top: 1.27rem;
    background-color: #6c41ec;
    text-align: center;
    font-size: .83rem;
    transition: background-color .25s;
    color: #fff;
    font-weight: 500;
  }
  
  .Playlist-save:hover {
    background-color: rgba(108, 65, 233, .7);
  }
  
  /* width and height of the scrollbar */
::-webkit-scrollbar {
    width: 8px;
    height: 8px;
  }
  
  /* background color of the scrollbar */
  ::-webkit-scrollbar-track {
    background-color: #000;
  }
  
  /* color of the scrollbar thumb */
  ::-webkit-scrollbar-thumb {
    background-image: linear-gradient(to bottom, #8e44ad, #0010);
    border-radius: 10px;
  }
  
  /* on hover of the thumb */
  ::-webkit-scrollbar-thumb:hover {
    background-image: linear-gradient(to bottom, #c0392b, #0010);
  }
  
  @media only screen and (max-width: 1020px) {
    .Playlist {
      width: 90%;
    }
  }