.Track {
    display: flex;
    flex-direction: row;
    align-items: center;
    background: linear-gradient(to left, rgba(107, 13, 255, .6) 20%, rgb(191 33 33 / 90%) 80%);
    border-radius: 0 25px 25px 0;
  }
  
  .Track-play {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .Track-action {
    cursor: pointer;
    padding: .5rem;
    font-size: 1.05rem;
    transition: color .25s;
    border: 0px;
    background-color: rgba(0, 0, 0, 0);
    color: #fff;
  }
  
  .Track-action:hover {
    color: rgba(265, 265, 265, .5);
  }
  
  .Track-information {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: left;
    height: auto;
  }
  
  .Track-information h3 {
    margin-bottom: .22rem;
  }
  
  .Track-information p {
    font-size: .83rem;
    font-weight: 300;
    color: rgba(256, 256, 256, 0.8);
  }

  a {
    text-decoration: none;
    color: #fff;
  }

  img {
    width: 3.33rem;
    height: 3.33rem;
    margin-right: 1.11rem;
    padding-left: 1rem;
  }