.SearchBar {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 6.94rem;
    margin-bottom: 6.33rem;

  }
  
  .SearchBar input {
    width: 287px;
    padding: .88rem 0;
    border: 1px solid #fff;
    border-radius: 1rem;
    margin-bottom: 2.22rem;
    color: white;
    text-align: center;
    font-size: 1.4rem;
    border: black 1rem solid;
    background: linear-gradient(to left, rgba(107, 13, 255) 20%, rgb(191 33 33 / 90%) 80%);
  }
  
  .SearchBar input:focus {
    outline: none;
  }
  
  .SearchBar input::placeholder {
    color: #fff;
  }

  .SearchButton {
    cursor: pointer;
    width: 8.11rem;
    padding: .77rem 0;
    border-radius: 54px;
    background-color: black;
    text-align: center;
    font-size: .833rem;
    transition: background-color .25s;
    border: 0px;
    color: #fff;
    font-weight: 500;
  }
  
  .SearchButton:hover {
    background: linear-gradient(to left, rgba(107, 13, 255) 20%, rgb(191 33 33 / 90%) 80%);
  }